<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder="用户名"
          size="small"
          style="width: 140px"
          v-model="searchParams.username"
          clearable
        ></el-input>
        <el-input
          placeholder="物流单号"
          size="small"
          style="width: 280px"
          v-model="searchParams.deliveryLogiNo"
          clearable
        ></el-input>
        <el-input
          placeholder="订单号"
          size="small"
          style="width: 280px"
          v-model="searchParams.deliveryOrderSn"
          clearable
        ></el-input>
        <el-select
          v-model="searchParams.deliveryStatus"
          clearable
          style="width: 140px"
          placeholder="状态"
          size="small"
        >
          <el-option v-for="(k,v,index) in deliveryStatusEnum" :key="index" :label="k" :value="v"></el-option>
        </el-select>
        <el-select
          v-model="searchParams.deliveryLogiCode"
          clearable
          style="width: 140px"
          placeholder="物流公司"
          size="small"
        >
          <el-option
            v-for="(item,index) in expressCompanyList"
            :key="index"
            :label="item.expressCorpName"
            :value="item.expressCorpId"
          ></el-option>
        </el-select>
        <el-date-picker
          v-model="searchParams.dateRange"
          size="small"
          type="datetimerange"
          :picker-options="pickerOptions"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          align="right"
        ></el-date-picker>
        <el-button type="success" size="small" @click="refresh()">查询</el-button>
        <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
      </div>
    </ToolBar>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :row-style="{height:'8px'}"
      :cell-style="{padding:'0px 0px'}"
    >
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left">
            <el-form-item label="备注">
              <span>{{ props.row.remark }}</span>
            </el-form-item>
            <el-form-item label="物流信息">
              <span>{{ props.row.deliveryLogiInformation }}</span>
            </el-form-item>
            <el-form-item label="创建时间">
              <span>{{ props.row.createTime }}</span>
            </el-form-item>
            <el-form-item label="更新时间">
              <span v-html=" props.row.updateTime"></span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="username" label="用户" width="120"></el-table-column>
      <el-table-column align="center" prop="expressCorpName" label="物流公司" width="90"></el-table-column>
      <el-table-column align="center" prop="deliveryLogiNo" label="物流单号" width="280"></el-table-column>
      <el-table-column
        align="center"
        prop="deliveryStatus"
        label="状态"
        width="80"
        :formatter="formatterStatus"
      ></el-table-column>
      <el-table-column align="center" prop="deliveryOrderSn" label="订单号" width="280"></el-table-column>
      <el-table-column align="center" prop="deliveryName" label="收货人" width="90"></el-table-column>
      <el-table-column align="center" prop="deliveryMobile" label="收货电话" width="120"></el-table-column>
      <el-table-column align="center" prop="deliveryAddress" label="收货地址"></el-table-column>
      <el-table-column align="center" prop="deliveryLogiFee" label="物流费" width="70"></el-table-column>
      <el-table-column align="center" prop="createTime" label="创建时间" width="160"></el-table-column>
      <el-table-column align="center" label="操作">
        <div slot-scope="s">
          <el-button type="primary" size="small" @click="edit(s.row)">详情</el-button>
          <el-button
            type="success"
            size="small"
            @click="clickConfirmDelivery(s.row)"
            v-if="s.row.deliveryStatus==2"
          >确认收货</el-button>
        </div>
      </el-table-column>
    </el-table>
    <Pagination
      :params="searchParams"
      :requestFunc="getLogiSticsList"
      ref="pagination"
      @returnData="returnData"
    />
  </div>
</template>

<script>
import {
  getLogiSticsList,
  editLogiSticsList,
  loadExpressComapny,
  confirmDelivery
} from "@/api/logistics/logisticsManage";
import { getDateList } from "@/utils/date";
import { DeliveryStatusEnum } from "@/utils/Immutable";
import { resetObject } from "@/utils/common";
export default {
  data() {
    return {
      searchParams: {
        username: "",
        deliveryLogiCode: "",
        deliveryLogiNo: "",
        deliveryOrderSn: "",
        deliveryStatus: "",
        deliveryLogiStatus: "",
        dateRange: []
      },
      getLogiSticsList: getLogiSticsList,
      titie: "",
      total: 10,
      pickerOptions: getDateList(),
      showEditDialog: false,
      tableData: [],
      deliveryStatusEnum: DeliveryStatusEnum,
      expressCompanyList: []
    };
  },
  mounted() {
    this.loadExpressComapny();
  },
  methods: {
    async loadExpressComapny() {
      let data = await loadExpressComapny();
      if (data.code != 0) {
        this.$message.error(data.msg);
        return;
      }
      this.expressCompanyList = data.data;
    },
    formatterStatus(a, b, v) {
      console.log(v);
      return this.deliveryStatusEnum[v];
    },
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    },

    refresh() {
      this.$refs.pagination.Refresh(); //分页刷新
      this.showEditDialog = false;
    },
    returnData(data) {
      this.tableData = data.map(v => {
        v.commentPicObj = v.commentPic ? JSON.parse(v.commentPic) : [];
        return v;
      });
      console.log(this.tableData);
    },
    clickConfirmDelivery(row) {
      row.deliveryStatus = 3;
      let text = `您确定收到货了吗？`;
      let _this = this;
      this.$confirm(text, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(v => {
          this.confirmDelivery(row);
        })
        .catch(() => {
          _this.refresh();
        });
    },
    async confirmDelivery(v) {
      let data = await confirmDelivery(v);
      if (data.code != 0) {
        this.$message.error(data.msg);
        this.refresh();
        return;
      }
      this.$message.success("修改成功");
    },
    edit(row, type) {
      this.titie = type == "recharge" ? "手动充值" : "手动扣款";
      this.showEditDialog = true;
      this.$refs.edit.update(row.id, type);
    }
  }
};
</script>
